/* eslint-disable */
import axios from 'axios'
// import bottom from '../components/bottom'
import Swiper from 'swiper/dist/js/swiper.min.js'
import 'swiper/dist/css/swiper.min.css';

export default ({
	name:'shareGoodsDetail',
	// components: {
	// 	bottom
	// },
	mounted () {
		this.initData()
	},
	data () {
		return {
			imgList:[],
			goods_name:'',
			goods_point:'',
			goods_num:'',
			detailList:[],
			shareOwner: this.$route.query.shareUserId,
			productId: this.$route.query.productId,
			shareSign: this.$route.query.shareSign,
			lunboHeight: document.documentElement.clientWidth / 2.3 +'px',
			timestamp:Date.parse(new Date()),
			title:this.$route.query.title?this.$route.query.title : ' ',
			shareContent: this.$route.query.content ? this.$route.query.content : ''
		}
	},methods:{
		initData: function() {
			const that = this;
			console.log("ininin")
			axios.get('/share/app/product/share/detail.do', {
				params: {
					productId: that.productId,
					shareUserId: that.shareOwner,
					shareSign: that.shareSign
				}
			})
			.then(function (result) {
				console.log(result)
				that.imgList = result.data.data.detailImages;
				that.goods_name = result.data.data.fullName;
				that.goods_point =  result.data.data.productRule.exchangePoints;
				that.goods_num =  result.data.data.soldNumber;
				that.detailList = result.data.data.productExtendedDetailList;
				that.$nextTick(function(){	// 
					let mySwiper = new Swiper('.swiper-container', {
						loop: true,
						speed: 1000,
						autoplay: {
							delay: 4000,
							disableOnInteraction: false
						},
						pagination: {
							el: '.swiper-pagination',
							clickable: true
						}
					})
				})
			})
			.catch(function (error) {
				console.log(error)
			});
		},
		wxShare: function() {
			const that = this;
			axios.get('/wx/ticket', {
				params: {
					'timeStamp':that.timeStamp,  
   					'url':window.location.href	
				}
			})
			.then(function (data) {
				console.log(data)
				wx.config({
				    debug: false, // ,apialertpclogpc
				    appId: 'wx3d1a1c125df0ae9a', // 
				    timestamp:that.timestamp , // 
				    nonceStr: data.nonceStr, // 
				    signature: data.signature,// 1
				    jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JSJS2
				});
				
				wx.ready(function () {  
				    wx.onMenuShareAppMessage({  
				      title: that.title,  
				      desc : shareContent,
				      link: shareUrl,  
				      imgUrl: shareImg,   
				      success: function () {
	//			      	alert("")
						// 
					  },
					  cancel: function () {
	//				  	alert("")
						// 
					  } 
				    });  
				    wx.onMenuShareTimeline({
					    title: shareTitle, // 
					    link: shareUrl, // JS
					    imgUrl: shareImg, // 
					    success: function () {
					    // 
						},
						cancel: function () {
						    // 
						}
					});
					wx.error(function (res) {  
					    //   alert(res.errMsg);  
					});  
				});
			})
			.catch(function (error) {
				console.log(error)
			});
		}
		
		
		
		
	}
})


