			const that = this;
			for(var i = 0; i < json.length; i++){
				if(json[i][0] == msg){
					const curObj = json[i];
					this.showMsgList.push(curObj[1])
					setTimeout(function() {
						console.log(that.showMsgList)
						that.showMsgList.push(curObj[2])
						setTimeout(function() {
							that.showMsgList.push(curObj[3])
						},1000)
